
import { defineComponent } from "vue";
import * as API from "../../API";

export default defineComponent({
  data() {
    return {
      email: "",
      password: "",
      username: "",
      register: false,
    };
  },
  methods: {
    login() {
      console.log("login");
      API.login(this.email, this.password);
    },
    createAcc() {
      console.log("register");
      try {
        API.register(this.email, this.password, this.username);
      } catch (e) {
        console.log({ error: e });
      } finally {
        this.email = "";
        this.password = "";
        this.username = "";
      }
    },
  },
});
